<template>
    <article class="p-3">
        <section class="d-flex pos-r">
            <canvas ref="canvas" style="width:100%;"></canvas>
            <div class="pos-a" style="width:50px; right:-20px;top:16px;">
                <button class="a-button a-button--dark ml-1" style="background:rgba(0,0,0,0.1); color:#000;" @click="photoTouch.zoom(-0.1)">
                    <i class="ion-md-remove font-1"></i>
                </button>
                <button class="a-button a-button--dark mt-1"  style="background:rgba(0,0,0,0.1); color:#000;" @click="photoTouch.zoom(0.1)">
                    <i class="ion-md-add font-1"></i>
                </button>

                <button class="a-button a-button--dark mt-1" style="background:rgba(0,0,0,0.1); color:#000;" @click="photoTouch.rotate(-10)">
                <i class="ion-md-refresh rotate-180 d-block font-1"></i>
                </button>
                <button class="a-button a-button--dark mt-1" style="background:rgba(0,0,0,0.1); color:#000;" @click="photoTouch.rotate(10)">
                    <i class="ion-md-refresh font-1"></i>
                </button>
            </div>
        </section>

        <div class="d-flex mt-1">
            <ButtonLoadFile
                class="a-button a-button--dark a-button--outline flex-1"
                @loaded="onImageLoaded"
            >Change Photo</ButtonLoadFile>

            <button class="a-button a-button--primary  flex-1">
                <i class="ion-md-checkmark font-1" style="vertical-align: sub;"></i> Done
            </button>
        </div>

        <section>
            <hr class="mt-2" />
            <!-- Offset -->
            <label class="a-input">
                <input
                    :value="offset[0]"
                    @input="photoTouch.moveTo(Number($event.target.value),offset[1])"
                    type="range"
                    max="400"
                />
                OffsetX: {{offset[0]}}
            </label>
            <label class="a-input">
                <input
                    :value="offset[1]"
                    @input="photoTouch.moveTo(offset[0],Number($event.target.value))"
                    type="range"
                    max="400"
                />
                OffsetY: {{offset[1]}}
            </label>
            <hr class="mt-2" />

            <label class="a-input">
                <input
                    :value="org[0]"
                    @input="photoTouch.changeOrg([org[0],Number($event.target.value)])"
                    type="range"
                    max="2000"
                />
                OX:{{org[0]}}
            </label>

            <label class="a-input">
                <input
                    :value="org[1]"
                    @input="photoTouch.changeOrg([Number($event.target.value), org[1]])"
                    type="range"
                    max="2000"
                />
                OY:{{org[1]}}
            </label>

            <label class="a-input">
                <input
                    :value="scale"
                    @input="photoTouch.zoomTo(Number($event.target.value))"
                    min="0.1"
                    max="2"
                    step="0.1"
                    type="range"
                />
                Scale: {{scale}}
            </label>
            <label class="a-input">
                <input
                    :value="angle"
                    @input="photoTouch.rotateTo(Number($event.target.value))"
                    type="range"
                    max="360"
                />
                A:{{angle}}
            </label>
        </section>
    </article>
</template>

<script>
import loadImage from './loadImage';
import PhotoTouch from '../../../packages/photo/dist/index.es';
import ButtonLoadFile from './ButtonLoadFile';
export default {
    name: 'DIY',

    components: { ButtonLoadFile },

    data() {
        return {
            photoTouch: null,
            overlayImgs: [],
            activeOverlay: {},
            img: null,
            org: [1432 / 2, 2216 / 2],
            angle: 0,
            scale: 1,
            offset: [0, 0],
            width: 1432,
            height: 2216,
            overlays: [
                [
                    'https://cdn.shopifycdn.net/s/files/1/0276/2922/4000/files/iPhone.png?v=1596445086',
                    'https://cdn.shopifycdn.net/s/files/1/0276/2922/4000/files/b059942a7321575b31567fab557b7626.png?v=1596445098',
                ],
                [
                    'https://cdn.shopifycdn.net/s/files/1/0276/2922/4000/files/01aee27d-3488-4e2f-9a8f-97a96928016d.png?v=1596503919',
                ],
            ],
        };
    },

    async mounted() {
        this.photoTouch = new PhotoTouch(this.$refs.canvas, this.width, this.height);
        this.photoTouch.on('transform', ({ offset, scale, angle, org }) => {
            this.offset = offset;
            this.scale = scale;
            this.angle = angle;
            this.org = org;
        });

        const imgs = await this.loadImgs(this.overlays[0]);
        this.photoTouch.changeOverlay(...imgs);
    },

    methods: {
        loadImgs(urls) {
            const p = [];
            for (const url of urls) {
                p.push(loadImage(url));
            }
            return Promise.all(p);
        },

        onImageLoaded(e) {
            const { img } = e[0].source;
            this.img = img;
            this.photoTouch.changeImg(img);
        },

        reset() {
            this.photoTouch.reset();
        },
    },
};
</script>

<style scope lang="scss">
canvas{
    cursor: move;
}
label {
    display: block;
}
</style>